@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins";
}

.dark-mode {
  background-color: #282c34;
}

.dark-mode .header {
  background-color: #282c34;

}

.dark-mode .header h3 {
  color: #fff;
}
.container {
  max-width: 1300px;
  margin: auto;
  padding: 20px;
  min-height: 100vh;
}

.notes-list {
  display: grid;
  grid-gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.note.new {
  background-color: #67d7cc;
}

.note textarea {
  border: none;
  outline: none;
  font-size: 15px;
  resize: none;
  background-color: transparent;
}

.note textarea::-webkit-input-placeholder {
  color: rgba(37, 37, 37, 0.822);
}

.note textarea:-ms-input-placeholder {
  color: rgba(37, 37, 37, 0.822);
}

.note textarea::placeholder {
  color: rgba(37, 37, 37, 0.822);
}

.save {
  padding: 5px 10px;
  border-radius: 15px;
  border: 1px solid rgb(14 14 14 / 10%);
  background-color: #e1e1e1;
  transition: 300ms;
}

.save:hover {
  background-color: #ededed;
}

.note {
  background-color: #fef68a;
  border-radius: 10px;
  padding: 1rem;
  min-height: 170px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  white-space: pre-wrap;
}

.note-date {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.note-date p {
  font-size: 15px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.note-words p {
  font-size: 15px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.note-words {
  display: flex;
  font-size: 15px;
}


.note-footer {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
}

.note-save {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.search {
  display: flex;
  grid-gap: 10px;
  align-items: center;
  background-color: rgb(233, 233, 233);
  border: 1px solid rgb(14 14 14 / 10%);
  border-radius: 20px;
  padding: 10px;
  margin-bottom: 1.5em;
}

.search input {
  width: 100%;
  border: none;
  outline: none;
  background-color: transparent;
}

.color-icon,
.delete-icon,
.save {
  cursor: pointer;
  outline: none;
}

.note-color {
  display: flex;
  align-items: center;
  grid-gap: 5px;
}

.note-color h3 {
  font-weight: 500;
}

.note-list-colors ul {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fit, minmax(24px, 1fr));
  grid-gap: 20px;
  align-items: center;
  transition: 300ms;
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
}

.note-list-colors.active {
  display: flex;
  position: absolute;
}

.note-list-color {
  cursor: pointer;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  list-style: none;
}

.note-text::-webkit-input-placeholder {
  color: rgba(37, 37, 37, 0.822);
}

.note-text:-ms-input-placeholder {
  color: rgba(37, 37, 37, 0.822);
}

.note-text::placeholder {
  color: rgba(37, 37, 37, 0.822);
}

.note-list-colors {
  display: none;
  flex-direction: column;
  width: 220px;
  background-color: #fff;
  padding: 10px;
  border-radius: 8px;
}

.note-list-colors h3 {
  text-align: center;
  font-weight: 500;
}
.header {
  top: 0;
  position: -webkit-sticky;
  position: sticky;
  background-color: #fff;
  -webkit-margin-after: 20px;
          margin-block-end: 20px;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  max-width: 1300px;
  margin: auto;
}

.header .logo {
  display: flex;
  align-items: center;
}

.header .logo h3 {
  font-weight: 500;
  font-size: 30px;
}

.header .logo img {
  width: 70px;
}

.App-logo {
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media (max-width:480px) {
  .header {
    margin-bottom: 10px;
  }

  .header .logo img {
    width: 50px;
  }

  .header .logo h3 {
    font-weight: 500;
    font-size: 22px;
  }
}
